    private _login() {
        this.base.login({
            success: (res) => {
                console.log(' ', res);
                if (res.gameToken) {
                    this.loginState = { code: res.gameToken, isLogin: true };
                    this.onLoginEnd.run();
                }
            },
            fail: (err) => {
                console.error(' ', err);
                this.loginState = { code: 'null', isLogin: false };
            }
        });
    }
