    private gameBox: View_HotGames;
    showGameBoxBannerAd() {
        if (LTSDK.instance.checkState == ECheckState.InCheck || !LTSDK.instance.configs.isNavEnable) return;
        if (!this.gameBox) {
            this.createGameBoxBannerAd();
        }
        if (this.gameBox) this.gameBox.ui.visible = true;
    }
    private createGameBoxBannerAd() {
        let ui = new fgui.GComponent();
        ui._height = 200;
        ui.width = 750;
        ui.x = 0;
        ui.y = Laya.stage.height - 220;
        fairygui.GRoot.inst.addChild(ui);
        this.gameBox = View_HotGames.CreateView(ui);
    }
    hideGameBoxBannerAd() {
        if (this.gameBox) this.gameBox.ui.visible = false;
    }
