import DefaultDevice from "../../DefaultDevice"

export default class TTDevice extends DefaultDevice {

    protected _base: any;

    constructor(base: any) {
        super();
        this._base = base;
    }

    Vibrate(isLong: boolean) {
        console.log("", isLong);
        if (isLong) {
            this._base.vibrateLong({
                success(res) { },
                fail(res) {
                    console.error("", res);
                },
                complete(res) { }
            });
        } else {
            this._base.vibrateShort({
                success(res) { },
                fail(res) {
                    console.error("", res);
                },
                complete(res) { }
            });
        }
    }

}