/*
 * @Desc: 
 *        Laya.EventDispatcher
 * 
 */

import EventDispatcher = Laya.EventDispatcher;


/**
 * <code>IEventDispatcher</code> 
 * : LayaEventDispatcher
 */
export interface IEventDispatcher {
    /**
     *  EventDispatcher 
     * @param	type 
     * @return  true false
     */
    // hasListener(type: string): boolean;
    /**
     * 
     * @param type	
     * @param data	<b></b> p1,p2,p3,...[p1,p2,p3,...]  p  p [p] p  p
     * @return  true false
     */
    event(type: string, data?: any): boolean;
    /**
     *  EventDispatcher 
     * @param type		
     * @param caller	
     * @param listener	
     * @param args		
     */
    on(type: string, caller: any, listener: Function, args?: Array<any>): void;
    /**
     *  EventDispatcher 
     * @param type		
     * @param caller	
     * @param listener	
     * @param args		
     */
    once(type: string, caller: any, listener: Function, args?: Array<any>): void;
    // _createListener(type: string, caller: any, listener: Function, args: Array<any>, once: boolean, offBefore?: boolean): EventDispatcher;
    /**
     *  EventDispatcher 
     * @param type		
     * @param caller	
     * @param listener	
     * @param onceOnly	 true , once 
     */
    off(type: string, caller: any, listener: Function, onceOnly?: boolean): void;
    /**
     *  EventDispatcher 
     * @param type	 null
     */
    offAll(type?: string): void;
    /**
     * callertarget
     * @param	caller caller
     */
    offAllCaller(caller: any): void;
    /**
     * 
     * @param	type 
     * @return	 true; false
     */
    // isMouseEvent(type: string): boolean;
}


/**
 * 
 */
export interface IRouter extends IEventDispatcher {
    /**
     * 
     * @param type	
     * @param data	<b></b> p1,p2,p3,...[p1,p2,p3,...]  p  p [p] p  p
     * @return  true false
     */
    route(type: string, data?: any): boolean;

    /**
     * 
     * @param type		
     * @param caller	
     * @param listener	
     * @param args		
     * @return  EventDispatcher 
     */
    onMsg(msgtype: number, caller: any, listener: Function, args?: Array<any>): void;

    /**
     * 
     * @param type		
     * @param caller	
     * @param listener	
     * @param onceOnly	 true , once 
     * @return  EventDispatcher 
     */
    offMsg(msgtype: number, caller: any, listener: Function): void;

    /**
     * 
     * @param type	 null
     * @return  EventDispatcher 
     */
    //offAllMsg(type?: string): void;

    /**
     * callertarget
     * @param	caller caller
     */
    offAllCallerMsg(caller: any): void;

    /**
     * 
     * @param type 
     * @param data 
     */
    routeMsg(msgtype: number, msg: any): void;
}

/**
 * 
 */
export class Router/* extends EventDispatcher*/ implements IRouter {
    private _evtDispatcher: EventDispatcher = new EventDispatcher();
    private _msgDispatcher: EventDispatcher = new EventDispatcher();

    /**
     * 
     * @param type	
     * @param data	<b></b> p1,p2,p3,...[p1,p2,p3,...]  p  p [p] p  p
     * @return  true false
     */
    event(type: string, data?: any): boolean {
        return this._evtDispatcher.event(type, data);
    }
    /**
     *  EventDispatcher 
     * @param type		
     * @param caller	
     * @param listener	
     * @param args		
     * @return  EventDispatcher 
     */
    on(type: string, caller: any, listener: Function, args?: Array<any>): void {
        this._evtDispatcher.on(type, caller, listener, args);
    }
    /**
     *  EventDispatcher 
     * @param type		
     * @param caller	
     * @param listener	
     * @param args		
     * @return  EventDispatcher 
     */
    once(type: string, caller: any, listener: Function, args?: Array<any>): void {
        this._evtDispatcher.once(type, caller, listener, args);
    }
    /**
     *  EventDispatcher 
     * @param type		
     * @param caller	
     * @param listener	
     * @param onceOnly	 true , once 
     * @return  EventDispatcher 
     */
    off(type: string, caller: any, listener: Function, onceOnly?: boolean): void {
        this._evtDispatcher.off(type, caller, listener, onceOnly);
    }
    /**
     *  EventDispatcher 
     * @param type	 null
     * @return  EventDispatcher 
     */
    offAll(type?: string): void {
        this._evtDispatcher.offAll(type);
    }
    /**
     * callertarget
     * @param	caller caller
     */
    offAllCaller(caller: any): void {
        this._evtDispatcher.offAllCaller(caller);
    }

    /**
     * 
     * @param type	
     * @param data	<b></b> p1,p2,p3,...[p1,p2,p3,...]  p  p [p] p  p
     * @return  true false
     */
    route(type: string, data?: any): boolean {
        if (data === void 0) return this.event(type);
        return this.event(type, data);
    }

    /**
     * 
     * @param type		
     * @param caller	
     * @param listener	
     * @param args		
     * @return  EventDispatcher 
     */
    onMsg(msgtype: number, caller: any, listener: Function, args?: Array<any>): void {
        this._msgDispatcher.on(msgtype.toString(), caller, listener, args);
    }

    /**
     *  EventDispatcher 
     * @param type		
     * @param caller	
     * @param listener	
     * @param onceOnly	 true , once 
     * @return  EventDispatcher 
     */
    offMsg(msgtype: number, caller: any, listener: Function): void {
        this._msgDispatcher.off(msgtype.toString(), caller, listener)
    }

    /**
     * callertarget
     * @param	caller caller
     */
    offAllCallerMsg(caller: any): void {
        this._msgDispatcher.offAllCaller(caller);
    }

    /**
     * 
     * @param type 
     * @param data 
     */
    routeMsg(msgtype: number, msg: any): void {
        this._msgDispatcher.event(msgtype.toString(), msg)
    }
}